import {Component, Input} from '@angular/core';
import {VisualizzaoffertaPage} from "../../pages/visualizzaofferta/visualizzaofferta";
import {HomecandidatoPage} from "../../pages/homecandidato/homecandidato";
import {AlertController, NavController, NavParams} from "ionic-angular";
import {Observable} from "rxjs/Observable";
import {AngularFireDatabase} from "angularfire2/database";


@Component({
  selector: 'inviocandidatura',
  templateUrl: 'inviocandidatura.html'
})
export class InviocandidaturaComponent {
    @Input() offertaselezionata: string;
    chiave : string;
    items: Observable<any>;
    database;
    today=Date.now();

    candidatura={id_candidato: 'FABIO',
        id_offerta: 'test',
        id_tecnico: 'CAZZZZZ',
        data: this.today ,
        titolo: 'vincenzoooooo',
        skill: 'asd',
        cv: 'asd',
        lettera: 'asd',
        note_recruiter: 'asd',
        note_tecnico: 'asd',
        note_candidato: '',
        giudizio: 'asd',
        data_colloquio: '03/11/1992/',
        ora_colloquio: '16:00',
        luogo_colloquio: 'Salerno',
        messaggio: "Salve, è stato selezionato per un colloquio presso la nostra sede a Salerno il giorno 5 marzo alle ore 16:00.",
        key:""};


    constructor(private db: AngularFireDatabase, public alertCtrl: AlertController, public navCtrl: NavController, public navParams: NavParams) {
        this.chiave=this.navParams.get('chiave');


    }

    onTapAnnulla() {
        this.navCtrl.push(VisualizzaoffertaPage);
    }

    onTapCandidati() {
        this.database=this.db.list('/candidature/candidature_da_verificare'),
            this.database.push(this.candidatura);
        this.candidatura.id_offerta=this.chiave;
        let alert = this.alertCtrl.create({
            title: 'Candidatura effettuata',
            subTitle: 'La tua richiesta di candidaturaidonea è stata inviata con successo!',
            buttons: ['OK']
        });
        alert.present();
        this.navCtrl.setRoot(HomecandidatoPage);

    }

}



