import {Component} from '@angular/core';
import {Candidato} from "../../app/candidato";
import {NavController} from "ionic-angular";
import {VisualizzacandidatoPage} from "../../pages/visualizzacandidato/visualizzacandidato";
import {AngularFireDatabase} from "angularfire2/database";

/**
 * Generated class for the CandidatoComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'candidato',
  templateUrl: 'candidato.html'
})
export class CandidatoComponent{
  candidatoselezionato: Candidato;
    public candidatilist: Array<any> = [];
    public candidato;
    database;


  constructor(private db: AngularFireDatabase, public navCtrl: NavController) {
      this.database = this.db.list('/candidati/');
      this.db.list('/candidati/', ref => ref.orderByChild('nome')).snapshotChanges().map(actions => {
          return actions.map(action => ({ key: action.key, ...action.payload.val() }));
      }).subscribe(items => {
          items.forEach(it => {
              this.candidato = new Candidato(it.fullname, it.email, it.telefono, it.password, it.key);
              this.candidatilist.push(this.candidato);
              console.log('Candidato: ', it.fullname, ' Email: ', it.email,
                  ' Telefono: ', it.telefono, ' password: ', it.password, ' key: ', it.key);
          })
      })
  }

    selezione(o: Candidato) {
        this.candidatoselezionato=o;
        this.navCtrl.push(VisualizzacandidatoPage,{candidatoselezionato: this.candidatoselezionato});
    }
}



