import {Component} from '@angular/core';
import {Offerta} from "../../app/offerta";
import {VisualizzaoffertaPage} from "../../pages/visualizzaofferta/visualizzaofferta";
import {NavController, NavParams} from "ionic-angular";
import { AngularFireDatabase} from "angularfire2/database";

@Component({
  selector: 'offerta',
  templateUrl: 'offerta.html'
})
export class OffertaComponent {
    public jobsofferlist: Array<any> = [];

    public ob;
    database;
    public offertaselezionata;
    o: Offerta;
    s:string;
    newItem = '';
    chiave: string;


    constructor(private db: AngularFireDatabase, public navCtrl: NavController, public navParams: NavParams) {
        this.database= this.db.list('/offerte/', ref => ref.orderByChild('titolo'));
        this.db.list('/offerte/', ref => ref.orderByChild('titolo')).snapshotChanges().map(actions => {
            return actions.map(action => ({ key: action.key, ...action.payload.val() }));
        }).subscribe(items => {
            items.forEach(it => {
                this.ob = new Offerta(it.titolo, it.luogodilavoro, it.skill, it.annuncio, it.titolodistudio, it.key);
                this.jobsofferlist.push(this.ob);
                console.log('Annuncio: ', it.annuncio, ' Luogo di lavoro: ', it.luogodilavoro,
                    ' titolo di studio: ', it.titolodistudio, ' key: ', it.key);
            })
        })
    }
    selezione(o: Offerta, chiave:string) {
        this.offertaselezionata=o;
        this.chiave=chiave;
        this.navCtrl.push(VisualizzaoffertaPage,{offertaselezionata: this.offertaselezionata , chiave: this.chiave});
    }


}



