import { Component } from '@angular/core';
import {NavController, NavParams} from "ionic-angular";
import {AngularFireDatabase} from "angularfire2/database";
import {Candidatura} from "../../app/candidatura";
//import {Candidatura} from "../../app/candidatura";

/**
 * Generated class for the CandidaturadaverificareComponent component.
 *
 * See https://angular.io/api/core/Component for more info on Angular
 * Components.
 */
@Component({
  selector: 'candidaturadaverificare',
  templateUrl: 'candidaturadaverificare.html'
})
export class CandidaturadaverificareComponent {
    candidaturaselezionata: Candidatura;
    public candidaturalist: Array<any> = [];
    public candidatura;
    database;


    constructor(private db: AngularFireDatabase, public navCtrl: NavController,public navParams: NavParams) {
        this.database = this.db.list('/candidature/candidature_non_idonee');
        this.db.list('/candidature/candidature_non_idonee', ref => ref.orderByChild('nome')).snapshotChanges().map(actions => {
            return actions.map(action => ({ key: action.key, ...action.payload.val() }));
        }).subscribe(items => {
            items.forEach(it => {
                this.candidatura = new Candidatura(it.id_candidato, it.id_offerta, it.id_tecnico, it.data, it.titolo, it.skill, it.cv, it.lettera, it.note_recruiter,
                    it.note_tecnico,it.note_candidato, it.giudizio, it.data_colloquio, it.ora_colloquio, it.luogo_colloquio,it.messaggio, it.key);
                this.candidaturalist.push(this.candidatura);

            })
        })
    }

}



