import { Component } from '@angular/core';
import {AlertController, NavController} from 'ionic-angular';
import {HomecandidatoPage} from "../homecandidato/homecandidato";
import {SignupPage} from "../signup/signup";
import {AuthService} from "../../app/auth.service";

@Component({
  selector: 'page-welcome',
  templateUrl: 'login.html'
})
export class LoginPage {
    candidato = {email: '', password: ''};
    valore: number;


    constructor(public authService: AuthService, public navCtrl: NavController, public alertCtrl: AlertController) {
        this.valore=0;
    }

    onLogin() {

        this.authService.login(this.candidato.email, this.candidato.password);
        console.log(this.valore);
        if (this.valore === 1) {

            let alert = this.alertCtrl.create({
                title: 'Login',
                subTitle: 'Login riuscito!',
                buttons: ['OK']
            });
            alert.present();
            this.candidato.email = this.candidato.password = '';

            this.navCtrl.setRoot(HomecandidatoPage);
        }
    }

    onSignup() {
        this.navCtrl.push(SignupPage);
    }

    onTapInfo() {
        this.navCtrl.push(SignupPage);
    }
}



