import { Component } from '@angular/core';
import {AlertController, NavController, NavParams} from 'ionic-angular';
import {VisualizzaoffertatecnicoPage} from "../visualizzaoffertatecnico/visualizzaoffertatecnico";
import {HometecnicoPage} from "../hometecnico/hometecnico";
import {Candidato} from "../../app/candidato";

@Component({
  selector: 'page-home',
  templateUrl: 'visualizzacandidato.html'
})
export class VisualizzacandidatoPage {
    candidatoselezionato:Candidato;

  constructor(public navCtrl: NavController, public alertCtrl: AlertController, public navParams: NavParams) {
      this.candidatoselezionato=navParams.get("candidatoselezionato");

  }

    onVisualizzaOfferta() {
        this.navCtrl.push(VisualizzaoffertatecnicoPage);
    }

    onInvia() {
        let alert = this.alertCtrl.create({
            title: 'Giudizio inviato',
            subTitle: 'Il giudizio è stato inviato al Recruiter con successo!',
            buttons: ['OK']
        });
        alert.present();
        this.navCtrl.push(HometecnicoPage);
    }

    onAnnulla() {
        this.navCtrl.push(HometecnicoPage);
    }
}



