export class Candidatura {
    id_candidato: string;
    id_offerta:string;
    id_tecnico:string;
    data:string;
    titolo:string;
    skill: string;
    cv:string;
    lettera:string;
    note_recruiter:string;
    note_tecnico:string;
    note_candidato:string;
    giudizio: string;
    data_colloquio:string;
    ora_colloquio:string;
    luogo_colloquio:string;
    messaggio:string;
    key:string;



    constructor(id_candidato: string, id_offerta: string, id_tecnico: string, data: string, titolo: string,
        skill: string, cv: string, lettera: string, note_recruiter: string, note_tecnico: string,note_candidato:string,  giudizio: string,
        data_colloquio: string, ora_colloquio: string,  luogo_colloquio,  messaggio:string, key:string){
        this.id_candidato= id_candidato;
        this.id_offerta= id_offerta;
        this.id_tecnico= id_tecnico;
        this.data= data;
        this.titolo=titolo;
        this.skill=skill;
        this.cv= cv;
        this.lettera=lettera;
        this.note_recruiter=note_recruiter;
        this.note_tecnico=note_tecnico;
        this.note_candidato=note_candidato;
        this.giudizio=giudizio;
        this.data_colloquio=data_colloquio;
        this.ora_colloquio=ora_colloquio;
        this.luogo_colloquio=luogo_colloquio;
        this.messaggio=messaggio;
        this.key=key;
    }
}


