import { Component } from '@angular/core';
import {AlertController, NavController} from 'ionic-angular';
import {NgForm} from "@angular/forms";
import {ProfiloPage} from "../profilo/profilo";
import {HomecandidatoPage} from "../homecandidato/homecandidato";

@Component({
  selector: 'page-home',
  templateUrl: 'modificaprofilo.html'
})
export class ModificaprofiloPage {

  constructor(public alertCtrl: AlertController,public navCtrl: NavController) {

  }

    onSignup(form: NgForm) {

        if (form.valid) {
            let alert = this.alertCtrl.create({
                title: 'Modifica',
                subTitle: 'La modifica dei dati personali è avvenuta con successo!',
                buttons: ['OK']
            });
            alert.present();

            this.navCtrl.setRoot(HomecandidatoPage);
        }
    }

    onAnnulla() {
        this.navCtrl.push(ProfiloPage);
    }
}



