export class Candidato {
    fullname: string;
    email:string;
    telefono:string;
    password:string;
    key:string;
    constructor(fullname: string,email: string, telefono: string, password: string, key:string){
        this.fullname = fullname;
        this.email = email;
        this.telefono = telefono;
        this.password = password;
        this.key = key;
    }
}


