import { Component, ViewChild } from '@angular/core';
import { Nav, Platform } from 'ionic-angular';
import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';

import { HomecandidatoPage } from '../pages/homecandidato/homecandidato';
import {HometecnicoPage} from "../pages/hometecnico/hometecnico";
import {ProfiloPage} from "../pages/profilo/profilo";
import {LoginPage} from "../pages/login/login";
import {SignupPage} from "../pages/signup/signup";
import {VisualizzacandidaturePage} from "../pages/visualizzacandidature/visualizzacandidature";
import {TutorialPage} from "../pages/tutorial/tutorial";


@Component({
  templateUrl: 'app.html'
})
export class MyApp {
  @ViewChild(Nav) nav: Nav;
  rootPage: any = HomecandidatoPage;

  pages: Array<{title: string, component: any, icon: string}>;

  constructor(public platform: Platform, public statusBar: StatusBar, public splashScreen: SplashScreen) {
    this.initializeApp();

    // used for an example of ngFor and navigation
    this.pages = [
        { title: 'Home candidato', component: HomecandidatoPage, icon: 'home' },
        { title: 'Home tecnico', component: HometecnicoPage, icon: 'home' },
        { title: 'Profilo', component: ProfiloPage, icon: 'person' },
        { title: 'Login', component: LoginPage, icon: 'log-in' },
        { title: 'Signup', component: SignupPage, icon:'person-add' },
        { title: 'Visualizza candidature', component: VisualizzacandidaturePage, icon: 'briefcase' },
        { title: 'Tutorial', component: TutorialPage, icon: 'play' }
        ];

  }

  initializeApp() {
    this.platform.ready().then(() => {
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }
}



